import React from 'react'
import { ToyPreview } from '../cmps/ToyPreview'

export function ToyList({ toys, onRemove }) {
    if (!toys || toys.length === 0) return <div>LOADING...</div>
    return (
        <div className="app-list flex justify-center align-center">
            {toys.map(toy => {
                return <ToyPreview key={toy._id} toy={toy} onRemove={onRemove} />
            })}
        </div>
    )
}