import React, { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { loadToys,removeToy } from '../store/actions/toy-action'
import { ToyFilter } from '../cmps/ToyFilter'
import { Link } from 'react-router-dom'
import { ToyList } from '../cmps/ToyList'

export function ToyApp() {

    const { toys } = useSelector(state => state.toyModule)
    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(loadToys())
    },[] )

    const onSetFilter = (filterBy) => {
        dispatch(loadToys(filterBy))
    }

    const onRemove = (toyId) => {
        dispatch(removeToy(toyId))
        dispatch(loadToys())
    }

    return (
        <div className="toy-app flex column">
            <ToyFilter onSetFilter={onSetFilter} />
            <Link to={`/toy/edit`}><span className="btn-edit">Edit New Toy</span></Link>
            <ToyList toys={toys} onRemove={onRemove} />
        </div>
    )
}