import React, { useState, useEffect } from 'react'
import { useParams } from "react-router-dom"
import { useHistory } from 'react-router'
import { saveToy,loadToys } from '../store/actions/toy-action'
import { useDispatch } from 'react-redux'
import { toyService } from '../services/toy-service'

export function ToyEdit() {
    const { toyId } = useParams()
    const [name, setName] = useState('')
    const [type, setType] = useState('')
    const [price, setPrice] = useState(20)
    const [inStock, setinStock] = useState(true)
    const dispatch = useDispatch()
    const history=useHistory()

    useEffect(() => {
        if (toyId) {
            const toy = toyService.getById(toyId)
            setName(toy.name)
            setType(toy.type)
            setPrice(toy.price)
            setinStock(toy.inStock)
        }
        return () => {
            setName('')
            setType('')
            setPrice(20)
            setinStock(true)
        }
    }, [toyId])

    const handleChange = (ev, str) => {
        switch (str) {
            case 'name':
                setName(ev.target.value)
                break;
            case 'type':
                setType(ev.target.value)
                break;
            case 'price':
                setPrice(ev.target.value)
                break;
            case 'instock':
                setinStock(ev.target.checked)
                break;
            default:
                console.log('it has a problem');
        }
    }
    const onSave = () => {
        const toyToSave = {
            name,
            type,
            price,
            inStock
        }
        dispatch(saveToy(toyToSave))
        dispatch(loadToys())
        history.push('/')
    }
    return (
        <div className="toy-edit flex column justify-center align-center">
            <h1>{toyId ? 'Update Toy' : 'Add Toy'}</h1>
            name:<input type="text" name="name" value={name}
                onChange={(ev) => handleChange(ev, 'name')} />
            type:<input type="text" name="type" value={type}
                onChange={(ev) => handleChange(ev, 'type')} />
            price:<input type="number" name="price" value={price}
                onChange={(ev) => handleChange(ev, 'price')} />
            <label> inStock:
                <input type="checkbox" name="inStock" checked={inStock}
                    value={inStock} onChange={(ev) => handleChange(ev, 'instock')} />
            </label>
            <button onClick={onSave}>save</button>
        </div>
    )
}