import React from 'react'
import { Link } from 'react-router-dom'
import { useParams } from "react-router-dom"
import { toyService } from '../services/toy-service'

export function ToyDetails() {
    const { toyId } = useParams()
    const toy = toyService.getById(toyId)
    return (
        <div className="toy-details flex column justify-center align-center">
            <span> name:{toy.name}</span>
            <span>price:{toy.price}</span>
            <span>in stock:{toy.inStock}</span>
            <span>type:{toy.type}</span>
            <img src={`https://robohash.org/${toy.name}.png`} alt='' />
            <Link to={`/toy/edit/${toy._id}`}>Edit</Link>
            <Link to="/">Go Back</Link>
        </div>
    )
}