import React from 'react'
import { Link } from 'react-router-dom'

export function ToyPreview({ toy, onRemove }) {
    
    const remove = () => {
        onRemove(toy._id)
    }
    return (
        <div className="preview">
            <button className="btn-remove" onClick={remove}>Remove Toy</button>
            <Link to={`/toy/details/${toy._id}`}>
                <div className="app-preview flex justify-center align-center">
                    <img src={`https://robohash.org/${toy.name}.png`} alt='' />
                    <div className="toy-content flex column">
                        <span>name: {toy.name}</span>
                        <span>price: {toy.price}</span>
                        <span>in stock: {toy.inStock ? '✅' : '❌'}</span>
                    </div>
                </div>
            </Link>
        </div>
    )
}