import { toyService } from "../../services/toy-service";

export function loadToys(filterBy) {
    // console.log('toys in action', filterBy);
    return (dispatch) => {
        try {
            const toys = toyService.query(filterBy)
            const action = {
                type: 'SET_TOYS',
                toys
            }
            dispatch(action)
        } catch (err) {
            console.log('error from catch loadToys', err);
        }
    }
}

export function removeToy(toyId) {
    return dispatch => {
        try {
           toyService.remove(toyId)
            const action = {
                type: 'REMOVE_TOY',
                toyId
            }
            dispatch(action)
        } catch (err) {
            console.log('error from catch remove toy', err);
        }
    }
}

export function saveToy(toy) { // Action Creator
    const type = toy._id ? 'UPDATE_TOY' : 'ADD_TOY'
    return (dispatch) => {
        try {
            const savedToy = toyService.save(toy)
            const action = {
                type,
                toy: savedToy
            }
            dispatch(action)
        } catch (err) {
            console.log('error from catch save toy', err);
        }
    }
}