import React, { useState } from 'react'

export function ToyFilter({ onSetFilter }) {

    const [input, setInput] = useState('')
    const [price, setPrice] = useState(20)
    const [inStock, setInStock] = useState('')

    const setFilter = (ev) => {
        const valuetoFilter = { name: input, price: price, inStock: inStock }

        let value = ev.target.value

        if (ev.target.type === 'range') {
            value = +ev.target.value
            setPrice(value)
            valuetoFilter.price = value
        } else if (ev.target.type === 'text') {
            setInput(value)
            valuetoFilter.name = value
        } else if (ev.target.type === 'checkbox') {
            setInStock(ev.target.checked)
            valuetoFilter.inStock = ev.target.checked
        }
        onSetFilter(valuetoFilter)
    }

    return (
        <div className="toy-filter flex align-center justify-center space-around">
            <label className="flex column justify-center align-center">Search By Name:
                <input className="txt-input" type="text" onChange={setFilter}
                    value={input} name='input' />
            </label>
            <label className="flex column justify-center align-center">Search By Price:&nbsp;
                <input type="range" min="1" max="500" onChange={setFilter}
                    value={price} name='price' />
                <span>{price}</span>
            </label>
            <label className="flex column justify-center align-center">
                <span>Available in Stock</span>
                <input type="checkbox" onChange={setFilter} name="inStock"
                    value={inStock} checked={inStock} />
            </label>
        </div>
    )
}