import toys from '../data/toy.json'
import { utilService } from './util-service.js'
export const toyService = {
    query,
    save,
    remove,
    getById
}

const gToys = toys

function query(filterBy = {}) {
    if (Object.keys(filterBy).length === 0) {
        return gToys
    } else {
        let filteredToys = (filterBy.name !== '') ? toys.filter((toy) => {
            return toy.name.toLowerCase().includes(filterBy.name.toLowerCase())
        }) : toys
        if (filterBy.price !== 0) filteredToys = filteredToys.filter((toy) => {
            return toy.price <= filterBy.price
        })
        if (filterBy.type) filteredToys = filteredToys.filter((toy) => {
            return toy.type.toLowerCase().includes(filterBy.type.toLowerCase())
        })
        if (filterBy.inStock) filteredToys = filteredToys.filter((toy) => {
            return toy.inStock === filterBy.inStock
        })
        return filteredToys
    }
}

function save(toyToSave) {
    if (toyToSave._id) {
        //update
        const toyIdx = gToys.findIndex(toy => toy._id === toyToSave._id)
        gToys.splice(toyIdx, 1, toyToSave)
        return toyToSave
    } else {
        //add
        const toy = _createToy(toyToSave.name, toyToSave.price, toyToSave.type, toyToSave.inStock)
        gToys.unshift(toy)
        return toyToSave
    }
}

function remove(toyId) {
    const toyIdx = gToys.findIndex(toy => toy._id === toyId)
    gToys.splice(toyIdx, 1)
    console.log(`toy:${toyId} removed`);
}

function getById(toyId) {
    return gToys.find(toy => toy._id === toyId)
}

function _createToy(name, price, type, inStock) {
    if (!price) price = utilService.getRandomIntInclusive(1, 200)
    return {
        _id: utilService.makeId(),
        name,
        price,
        type,
        inStock,
        createdAt: Date.now()
    }
}